html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: AvenirNext-Regular;
  src: url("fonts/AvenirNext-Regular.woff2") format("woff2"), url("fonts/AvenirNext-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: AvenirNext-DemiBold;
  src: url("fonts/AvenirNext-DemiBold.woff2") format("woff2"), url("fonts/AvenirNext-DemiBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'AvenirNext-Regular', serif;
  font-size: 25px;
  color: #5D90A3;
  letter-spacing: 1.52px;
}

.center {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 55px;
}

.header {
  padding: 20px;
  background: #5D90A3;
}

.logo {
  width: 121px;
  height: 52px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.container {
  padding: 52px 0;
  background: #2C3235;
}

.title {
  margin-bottom: 36px;
  font-size: 32px;
  color: white;
}

.title_custom {
  font-family: 'AvenirNext-DemiBold', serif;
  color: #72BA8A;
}

.title + .text {
  margin-top: -12px;
}

.text {
  margin-bottom: 50px;
}

.counter {
  font-family: 'AvenirNext-DemiBold', serif;
  color: #5D90A3;
}

.comments {
  margin-bottom: 70px;
}

.comments__item {
  margin-bottom: 32px;
  padding: 23px 14px 25px;
  background: white;
  box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.06);
  border-radius: 4px;
}

.comments__head {
  display: flex;
  margin-bottom: 12px;
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: 16px;
  color: #2C3235;
  letter-spacing: 0.2px;
  line-height: 1.25;
}

.comments__meta {
  min-height: 30px;
  padding: 5px 0 5px 45px;
  background-repeat: no-repeat;
  background-position: 0 50%;
  background-size: 30px 30px;
}

.comments__meta.happy {
  background-image: url(data:image/svg+xml;base64,);
}

.comments__meta.sad {
  background-image: url(data:image/svg+xml;base64,);
}

.comments__location {
  min-height: 30px;
  margin-left: auto;
  padding: 5px 45px 5px 0;
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 30px 30px;
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: 16px;
  color: black;
  letter-spacing: 0.3px;
}

.comments__body {
  padding: 27px 23px;
  background: #F8F8F8;
  border-radius: 4px;
  font-size: 25px;
  color: #2C3235;
  letter-spacing: 0.36px;
}

.hr {
  margin: 70px 0 64px;
  height: 2px;
  background: #979797;
}

.btn {
  display: inline-block;
  min-width: 276px;
  padding: 20px 45px;
  background: #72BA8A;
  border: 1px solid #72BA8A;
  border-radius: 4px;
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: 25px;
  color: #FFFFFF;
  letter-spacing: 1.43px;
  text-align: center;
}

.btn_sm {
  padding: 14px 40px;
  border-radius: 6px;
  font-size: 20px;
  letter-spacing: 1.52px;
}

.btn_red {
  background: #B86578;
  border-color: #B86578;
}

.buttons {
  display: flex;
  margin-bottom: 66px;
}

.buttons .btn:not(:last-child) {
  margin-right: 30px;
}

.form {
  margin-bottom: 100px;
}

.field {
  display: flex;
  margin-bottom: 72px;
  align-items: center;
}

.field__label {
  margin-right: 55px;
  font-size: 25px;
  color: #5D90A3;
  letter-spacing: 1.52px;
}

.field__wrap {
  flex: 1;
}

.field__input {
  width: 100%;
  height: 77px;
  padding: 0 25px;
  border: 1px solid #95989A;
  border-radius: 4px;
  font-family: 'AvenirNext-Regular', serif;
  font-size: 32px;
  color: #2C3235;
  letter-spacing: 0.2px;
}

.router {
  margin-bottom: 80px;
}

.router__item {
  display: flex;
  margin-bottom: 24px;
  align-items: center;
}

.router__col {
  font-size: 30px;
  color: #FFFFFF;
  letter-spacing: 1.52px;
}

.router__col:first-child {
  margin-right: 47px;
  flex: 0 0 171px;
}

.router .btn {
  min-width: 0;
  width: 100%;
}

.section {
  margin-bottom: 64px;
}

.toggle {
  position: relative;
  user-select: none;
  cursor: pointer;
}

.toggle__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.toggle__input:checked + .toggle__wrap .toggle__text:first-child {
  color: #D8D8D8;
}

.toggle__input:checked + .toggle__wrap .toggle__text:last-child {
  color: #72BA8A;
}

.toggle__input:checked + .toggle__wrap .toggle__line:before {
  background: #72BA8A;
  transform: translateX(62px);
}

.toggle__wrap {
  display: flex;
  align-items: center;
}

.toggle__preview {
  display: flex;
  align-items: center;
  flex: 0 0 258px;
}

.toggle__text {
  font-family: 'AvenirNext-DemiBold', serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #D8D8D8;
  transition: color .2s;
}

.toggle__text:first-child {
  color: #B86578;
}

.toggle__line {
  position: relative;
  height: 14px;
  margin: 0 10px;
  background: #FFFFFF;
  border: 1px solid #D8D8D8;
  border-radius: 7px;
  flex: 0 0 100px;
}

.toggle__line:before {
  content: '';
  position: absolute;
  top: -15px;
  left: -4px;
  width: 42px;
  height: 42px;
  background: #B86578;
  border-radius: 50%;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
  transition: transform .2s;
}

.toggle__content {
  font-size: 30px;
  color: white;
  letter-spacing: 1.52px;
}

.switch {
  display: block;
  position: relative;
  margin-bottom: 30px;
}

.switch__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.switch__input:checked + .switch__content:after {
  opacity: 1;
}

.switch__content {
  display: block;
  min-height: 48px;
  padding: 5px 0 2px 258px;
  font-size: 30px;
  color: #FFFFFF;
  letter-spacing: 1.52px;
  user-select: none;
  cursor: pointer;
}

.switch__content:before, .switch__content:after {
  content: '';
  position: absolute;
}

.switch__content:before {
  top: 0;
  left: 100px;
  width: 48px;
  height: 48px;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: contain;
}

.switch__content:after {
  top: 6px;
  left: 109px;
  width: 39px;
  height: 30px;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0;
  transition: opacity .2s;
}

.printer__item {
  margin-bottom: 80px;
}

.printer__body {
  margin-top: 30px;
}

.printer .text {
  margin-bottom: 44px;
  padding-left: 258px;
  font-size: 30px;
  letter-spacing: 1.52px;
}
