@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(AvenirNext-Regular, AvenirNext-Regular);
@mixin f {
  font-family: 'AvenirNext-Regular', serif; }
@include font(AvenirNext-DemiBold, AvenirNext-DemiBold);
@mixin fb {
  font-family: 'AvenirNext-DemiBold', serif; }

body {
  @include f;
  font-size: 25px;
  color: #5D90A3;
  letter-spacing: 1.52px; }

.center {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 55px; }

.header {
  padding: 20px;
  background: #5D90A3; }

.logo {
  width: 121px;
  height: 52px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain; }

.container {
  padding: 52px 0;
  background: #2C3235; }

.title {
  margin-bottom: 36px;
  font-size: 32px;
  color: white;
  &_custom {
    @include fb;
    color: #72BA8A; } }

.title + .text {
  margin-top: -12px; }

.text {
  margin-bottom: 50px; }

.counter {
  @include fb;
  color: #5D90A3; }

.comments {
  margin-bottom: 70px;
  &__item {
    margin-bottom: 32px;
    padding: 23px 14px 25px;
    background: white;
    box-shadow: 0 5px 20px 2px rgba(0,0,0,0.06);
    border-radius: 4px; }
  &__head {
    display: flex;
    margin-bottom: 12px;
    @include fb;
    font-size: 16px;
    color: #2C3235;
    letter-spacing: 0.2px;
    line-height: 1.25; }
  &__meta {
    min-height: 30px;
    padding: 5px 0 5px 45px;
    background-repeat: no-repeat;
    background-position: 0 50%;
    background-size: 30px 30px;
    &.happy {
      background-image: url(data:image/svg+xml;base64,); }
    &.sad {
      background-image: url(data:image/svg+xml;base64,); } }
  &__location {
    min-height: 30px;
    margin-left: auto;
    padding: 5px 45px 5px 0;
    background-repeat: no-repeat;
    background-position: 100% 50%;
    background-image: url(data:image/svg+xml;base64,);
    background-size: 30px 30px;
    @include fb;
    font-size: 16px;
    color: black;
    letter-spacing: 0.3px; }
  &__body {
    padding: 27px 23px;
    background: #F8F8F8;
    border-radius: 4px;
    font-size: 25px;
    color: #2C3235;
    letter-spacing: 0.36px; } }

.hr {
  margin: 70px 0 64px;
  height: 2px;
  background: #979797; }

.btn {
  display: inline-block;
  min-width: 276px;
  padding: 20px 45px;
  background: #72BA8A;
  border: 1px solid #72BA8A;
  border-radius: 4px;
  @include fb;
  font-size: 25px;
  color: #FFFFFF;
  letter-spacing: 1.43px;
  text-align: center;
  &_sm {
    padding: 14px 40px;
    border-radius: 6px;
    font-size: 20px;
    letter-spacing: 1.52px; }
  &_red {
    background: #B86578;
    border-color: #B86578; } }

.buttons {
  display: flex;
  margin-bottom: 66px;
  .btn {
    &:not(:last-child) {
      margin-right: 30px; } } }

.form {
  margin-bottom: 100px; }

.field {
  display: flex;
  margin-bottom: 72px;
  align-items: center;
  &__label {
    margin-right: 55px;
    font-size: 25px;
    color: #5D90A3;
    letter-spacing: 1.52px; }
  &__wrap {
    flex: 1; }
  &__input {
    width: 100%;
    height: 77px;
    padding: 0 25px;
    border: 1px solid #95989A;
    border-radius: 4px;
    @include f;
    font-size: 32px;
    color: #2C3235;
    letter-spacing: 0.2px; } }

.router {
  margin-bottom: 80px;
  &__item {
    display: flex;
    margin-bottom: 24px;
    align-items: center; }
  &__col {
    font-size: 30px;
    color: #FFFFFF;
    letter-spacing: 1.52px;
    &:first-child {
      margin-right: 47px;
      flex: 0 0 171px; } }
  .btn {
    min-width: 0;
    width: 100%; } }

.section {
  margin-bottom: 64px; }

.toggle {
  position: relative;
  user-select: none;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__wrap &__text {
    &:first-child {
      color: #D8D8D8; }
    &:last-child {
      color: #72BA8A; } }
  &__input:checked + &__wrap &__line {
    &:before {
      background: #72BA8A;
      transform: translateX(62px); } }
  &__wrap {
    display: flex;
    align-items: center; }
  &__preview {
    display: flex;
    align-items: center;
    flex: 0 0 258px; }
  &__text {
    @include fb;
    font-size: 14px;
    text-transform: uppercase;
    color: #D8D8D8;
    transition: color .2s;
    &:first-child {
      color: #B86578; } }
  &__line {
    position: relative;
    height: 14px;
    margin: 0 10px;
    background: #FFFFFF;
    border: 1px solid #D8D8D8;
    border-radius: 7px;
    flex: 0 0 100px;
    &:before {
      content: '';
      position: absolute;
      top: -15px;
      left: -4px;
      width: 42px;
      height: 42px;
      background: #B86578;
      border-radius: 50%;
      box-shadow: 0 1px 2px 0 rgba(0,0,0,0.50);
      transition: transform .2s; } }
  &__content {
    font-size: 30px;
    color: white;
    letter-spacing: 1.52px; } }

.switch {
  display: block;
  position: relative;
  margin-bottom: 30px;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__content {
    &:after {
      opacity: 1; } }
  &__content {
    display: block;
    min-height: 48px;
    padding: 5px 0 2px 258px;
    font-size: 30px;
    color: #FFFFFF;
    letter-spacing: 1.52px;
    user-select: none;
    cursor: pointer;
    &:before,
    &:after {
      content: '';
      position: absolute; }
    &:before {
      top: 0;
      left: 100px;
      width: 48px;
      height: 48px;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: contain; }
    &:after {
      top: 6px;
      left: 109px;
      width: 39px;
      height: 30px;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: contain;
      opacity: 0;
      transition: opacity .2s; } } }

.printer {
  &__item {
    margin-bottom: 80px; }
  &__body {
    margin-top: 30px; }
  .text {
    margin-bottom: 44px;
    padding-left: 258px;
    font-size: 30px;
    letter-spacing: 1.52px; } }


